<section class="header" id="header"><div class="container"><div class="header-wrap"><my-header-info :info="header" @clickToTimeline="scrollToTimeline" @clickToPage="roadToPage"></my-header-info><my-header-avatar :skillsAvatar="header.mySkillsAvatar" :master="header.master"></my-header-avatar></div></div><div class="big-container"><div class="header__slide"><splide :options="header.cert"><splide-slide v-for="item in header.certSlides" :key="item" @click="fullPageAdd($event)"><img :src="require('@/assets/img/' + item.src)" :alt="item.alt"></splide-slide></splide></div></div><div class="container"><my-header-bg :myEmail="myEmail" :headerBg="header" @clickToPage="roadToPage"></my-header-bg></div></section>