<div class="header__info"><h2 class="header__info-title neon" v-html="info.neonHeaderTitleFirst"></h2><h2 class="header__info-title neon" v-html="info.neonHeaderTitleSecond"></h2><p class="header__info-subtitle">{{ info.mySkillsSub }}</p><div class="header__info-button"><my-btn :btnInfo="info.headerTextBtn.btnTimeline" @clickAction="$emit('clickToTimeline')"></my-btn><my-btn :btnInfo="info.headerTextBtn.btnContact" @clickAction="$emit('clickToPage', info.headerTextBtn.btnContact.link)"></my-btn></div></div>