<section class="contact"><canvas id="pretty-bg"></canvas><div class="container"><div class="contact-wrap"><h3 class="contact-title neon" v-html="contact.neonContactTitle"></h3><div class="contact-body"><div class="contact__info"><p class="contact__info-desc">{{ contact.contactInfoDesc }}</p><div class="contact__info-table"><ol class="contact__info-table-list"><li class="contact__info-table-list-item" v-for="item in contact.contactTableList" :key="item"> <p class="contact__info-table-list-text">{{ item.text }}</p><a class="contact__info-table-list-link" :href="item.linkTipy + item.link">{{ item.title }}</a></li></ol></div><div class="contact__info-social header__bg-social"><my-social-icon></my-social-icon></div></div><div class="contact__form-wrap"><contact-form></contact-form></div></div></div></div></section>