<aside class="sidebar" id="sidebar"><div class="sidebar-wrap"><h2 class="sidebar__title">{{ sidebarTitle }}</h2><div class="sidebar-wrap-bg"><img :src="require('@/assets/img/' + sidebarBg.src)" :alt="sidebarBg.alt"></div><div class="sidebar__links"><ul class="sidebar__links-col"><li class="sidebar__links-item" v-for="item in sidebarLinks" :key="item"> <router-link class="sidebar__links-item-link" :to="item.link" @click="burgerActiveMet(), showDialog()"><span>{{ item.text }}</span></router-link></li></ul></div><div class="sidebar__social"><my-social-icon></my-social-icon></div><div class="sidebar__btn"><night-mode></night-mode></div></div></aside>