<section class="portfolio" id="portfolio"><div class="container"><div class="portfolio-wrap"><h3 class="portfolio__title neon" v-html="portfolio.neonPortfolioTitle"></h3><div class="portfolio__card"><my-card v-for="item in firstPortfolioCards" :key="item" :cardInfo="item"></my-card></div><div class="portfolio__btn"><my-btn :btnInfo="portfolio.btn" @clickAction="roadToPage(portfolio.btn.link)"></my-btn></div></div></div><div class="portfolio__background"></div></section>