<div class="header__bg-wrap"><div class="header__bg-item"><div class="header__bg-title neon" v-html="headerBg.neonHeaderTitleBgFirst"></div><div class="header__bg-skills"><ul><li v-for="item in headerBg.mySkills">{{ item }}</li></ul></div><div class="header__bg-button"><my-btn :btnInfo="headerBg.headerBgBtn" @clickAction="$emit('clickToPage', headerBg.headerBgBtn.link)"></my-btn></div></div><div class="header__bg-item"><div class="header__bg-title neon" v-html="headerBg.neonHeaderTitleBgSecond"></div><div class="header__bg-social"><my-social-icon></my-social-icon></div><a class="header__bg-email" :href="'mailto:' + myEmail">{{ myEmail }}</a></div></div>