<section class="timeline" id="timeline"><div class="container"><div class="timeline-wrap"><h3 class="timeline__title neon" v-html="neonTimelineTitle"> </h3><div class="timeline__column"><div class="timeline__row" v-for="(item, idx) in timelineItems" :key="item"><div class="timeline__row-img"> <div class="timeline-icon" :class="'timeline-icon_' + (idx + 1)"></div></div><div class="timeline__row-text"><h4 class="timeline__row-text-title">{{ item.title }}</h4><p class="timeline__row-text-subtitle">{{ item.subtitle }}</p><span class="timeline__row-text-desc">{{ item.desc }}</span></div></div></div></div></div></section>