<div class="card white-color" :id="cardInfo.cardId" @mouseenter="randColor($event)"><div class="card__img-wrap"><button class="card__btn" @click="openCard($event, '/project/card')">Подробнее</button><div class="card__img"><figure class="card__img-front"><img v-lazy="{ src: cardInfo.cardSrc }" :alt="cardInfo.cardAlt"></figure><figure class="card__img-back"><img v-lazy="{ src: cardInfo.cardSrc }" :alt="cardInfo.cardAlt"></figure></div></div><div class="card__text"><h4 class="card__title">{{ cardInfo.cardTitle }}</h4><p class="card__subtitle">{{ cardInfo.cardText }}</p></div></div>