<form class="contact__form" id="form-contact" data-card="1" @submit.prevent="submitMessageForm()"><h3 class="contact__form-title">{{ contact.title }}</h3><div class="contact__form-item" id="email-form-wrap"><input class="form-input-email" id="email-form" type="email" placeholder="Ваш Email:" form="form-contact" name="email-contact" v-model="formAnswers.email"></div><div class="contact__form-item" id="theme-form-wrap"><input class="form-input-theme" id="theme-form" type="text" placeholder="Тема письма:" form="form-contact" name="theme-contact" v-model="formAnswers.theme"></div><div class="contact__form-item" id="text-form-wrap"><textarea class="form-textarea-text" id="text-form" name="comment-contact" cols="30" rows="5" placeholder="Сообщение:" form="form-contact" v-model="formAnswers.text"></textarea></div><div class="contact__form-button"><my-btn :btnInfo="contact.btnSubmit"></my-btn></div></form>